.App {
  font-family: Arial, sans-serif;
  padding: 20px;
  color: #333;
  max-width: 1000px;
  margin: 0 auto;
}

/* Profile Section */
.App-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.profile-text h1 {
  font-size: 18px;
  color: #888;
  margin: 0;
}

.profile-text h2 {
  font-size: 28px;
  font-weight: bold;
  color: #333;
}

.profile-text p {
  font-size: 16px;
  max-width: 600px;
  color: #555;
}

/* About Statement */
.about-statement {
  margin: 40px 0;
}

.about-statement h3 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

.about-statement p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
}

/* Projects Section */
.projects-section {
  margin-top: 40px;
}

.projects-section h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

.project-card {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project-image {
  width: 150px;
  height: auto;
  margin-right: 20px;
  border-radius: 8px;
}

.project-info h3 {
  font-size: 22px;
  margin: 0;
}

.project-info p {
  font-size: 14px;
  color: #666;
}
